<template lang="pug">
    v-card 
        v-card-text
            v-card-title
                span 成交订单及利润统计
            v-container
                v-row(dense) 
                    v-col(cols="12" sm="4")
                        v-menu(
                            ref="menu1"
                            v-model="menu1"
                            :close-on-content-click="false"
                            transition="scale-transition"
                            offset-y
                            max-width="290px"
                            min-width="290px"
                        )
                            template(v-slot:activator="{ on, attrs }")
                                v-text-field(
                                    outlined 
                                    hide-details 
                                    v-model="date"  
                                    label="开始日期" prepend-icon="mdi-calendar"
                                    v-bind="attrs"
                                    v-on="on"
                                )
                            v-date-picker(
                                v-model="date"
                                no-title
                                @input="menu1 = false"
                            )

                    v-col(cols="12" sm="4")
                        v-text-field(outlined hide-details v-model="coin"  label="目标币种")
                    v-col(cols="12" sm="4")
                        v-text-field(outlined hide-details v-model="fee"  label="手续费")
                    
                    v-col(cols="12" sm="4")
                        v-text-field(outlined hide-details v-model="ret.bidAmount" disabled label="买单数量")
                    v-col(cols="12" sm="4")
                        v-text-field(outlined hide-details v-model="ret.bidPrice" disabled label="买单均价")
                    v-col(cols="12" sm="4")
                        v-text-field(outlined hide-details v-model="ret.bidMoney" disabled label="买单金额")
                    v-col(cols="12" sm="4")
                        v-text-field(outlined hide-details v-model="ret.askAmount" disabled label="卖单数量")
                    v-col(cols="12" sm="4")
                        v-text-field(outlined hide-details v-model="ret.askPrice" disabled label="卖单均价")
                    v-col(cols="12" sm="4")
                        v-text-field(outlined hide-details v-model="ret.askMoney" disabled label="卖单金额")
                    v-col(cols="12" sm="4")
                        v-text-field(outlined hide-details v-model="ret.profitRatio" disabled label="利润率")
                    v-col(cols="12" sm="4")
                        v-text-field(outlined hide-details v-model="ret.profitMoney" disabled label="预估利润")
                    v-col(cols="12" sm="4")
                        v-text-field(outlined hide-details v-model="ret.endDate" disabled label="截止日期")
                    v-col(cols="12" sm="12")

                v-divider

                v-row
                    v-col(cols="12" sm="4" md="4" lg="4")
                        v-btn(class="mb-2 text-capitalize" color="primary" @click="cal_profit" )
                            span 计算利润

                    v-col(cols="12" sm="4" md="4" lg="4")
                        v-btn(class="mb-2 text-capitalize" color="primary" @click="update_col_page_data" )
                            v-icon(class="mr-2" ) mdi-reload
                            span 刷新
                            

                v-data-table(
                    :headers="headers"
                    :items="tabledata"
                    :options.sync="options"
                    no-data-text="暂无数据"
                    dense
                    :server-items-length="total_rows"
                    :footer-props="{'items-per-page-options': items_per_page_options,'show-first-last-page':true}"
                )
                    template(v-slot:item.createtime="{item}")
                        span {{getDateStr(item)}}

                            
                            
                                
                                
                    
</template>

<script>
import * as api from "@/apis/wswebapi"
import { get} from 'vuex-pathify'
export default {
    data: () => ({
    tabledata:[
        ],
    
    options:{},
    items_per_page_options:[10,20,50],
    total_rows:100,
    headers:[{text:"数字币",value:"tarcoin"},{text:"基础币",value:"basecoin"},{text:"下单类型",value:"side"},
    {text:"价格",value:"price"},{text:"数量",value:"amount"},{text:"时间",value:"createtime"},{text:"订单号",value:"orderid"},
    {text:"策略",value:"ordertype"}],
    date: new Date().toISOString().substr(0, 10),
    menu1: false,
    menu2: false,
    balance: "",
    startDate:0,
    fee:0,
    coin: "usdt",
    ret:{
        "askAmount": 0,
        "askMoney": 0,
        "askPrice": 0,
        "bidAmount": 0,
        "bidMoney": 0,
        "bidPrice": 0,
        "endDate": 0,
        "fee": 0,
        "profitMoney": 0,
        "profitRatio": 0,
        "startDate": "2020-11-11"
        },
    coins:[],
    ExName:"WKJ"
    }),
    computed:{
        isLogin:get('user/islogin'),
        exbotisexpire:get('user/exbotisexpire'),
        username:get('user/username'),
        
    },


    methods:{
        getDateStr(item){
            let date = new Date((Number(item.createtime)+3600*8)*1000);
            return date.toISOString().substr(0, 19)
        },
        cal_profit(){
            if (!this.isLogin){
                this.$store.dispatch("msglog/push_msg",{message:"用户未登陆，请登陆后重试",type:"error"});
                return
            }
            if (this.exbotisexpire){
                this.$store.dispatch("msglog/push_msg",{message:"软件已过期，请联系管理员",type:"error"});
                return
            }
            let params = {
                "exname":this.ExName,
                "username":this.username,
                "tarcoin":this.coin,
                "FeeRatio":Number(this.fee),
                "startDate":this.date,
            };
            api.DBMCalUserProfit(params).then(res=>{
                if (res.code==200){
                    this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"success"});
                    this.ret=res.data;
                }else{
                    this.$store.dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"})
                }
            })
        },
        update_col_page_data(){
            let page=this.options.page;
            let pagesize=this.options.itemsPerPage;
            api.DBMGetUserOrders({page:page,pagesize:pagesize}).then(res=>{
                if (res.code==200){
                    this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"success"},{root:true});
                    this.tabledata=res.data;
                    this.total_rows=res.total;
                }else{
                    this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"error"},{root:true})
                }
            })
        },
        download_page_data(){

        },
        submit(){
            let params = {
                            "Coin":this.coin,
                            "ExName":this.ExName};
            api.GetCoinBalance(params).then(res=>{
        if (res.code==200){
            this.balance=res.data;
            this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"success"});
        }else{
            this.$store.dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"})
        }
    })
        }
        
    },
    watch:{
        options:{
            handler(){
                this.update_col_page_data();
            },
            deep:true,
        },
    }
}
</script>